.echarts {
	width: 100%;
	height: 100%;
}
.dataScreen-container {
	width: 100%;
	height: 100%;
	background: url("./images/bg.webp") no-repeat;
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
	background-size: 100% 100%;
	background-size: cover;
	.container {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
	}
	.dataScreen {
		position: fixed;
		top: 50%;
		left: 50%;
		z-index: 999;
		display: flex;
		flex-direction: column;
		overflow: hidden;
		transition: all 0.3s;
		transform-origin: left top;
		.dataScreen-header {
			display: flex;
			width: 100%;
			height: 38px;
			//top2bottom
			opacity: 0;
			transform: translateY(-100px); 
			animation-name: top2bottom;
			animation-duration: 1s;
			animation-iteration-count: 1;
			animation-delay: 2s;
			animation-fill-mode: forwards;
			@keyframes top2bottom {
				0% {
					opacity: 0;
					transform: translateY(-100px); 
				}
				100% {
					opacity: 1;
					transform: translateY(0);
				}
			}
			.header-lf,
			.header-rg {
				position: relative;
				width: 567px;
				height: 100%;
				background: url("./images/dataScreen-header-left-bg.webp") no-repeat;
				background-size: 100% 100%;
			}
			.header-ct {
				position: relative;
				flex: 1;
				height: 100%;
				.header-ct-title {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 82px;
					font-family: YouSheBiaoTiHei;
					font-size: 32px;
					line-height: 78px;
					color: #05e8fe;
					text-align: center;
					letter-spacing: 4px;
					background: url("./images/dataScreen-header-center-bg.webp") no-repeat;
					background-size: 100% 100%;
					z-index: 1;
					.redo {
						cursor: pointer;
					}
					.header-ct-warning {
						position: absolute;
						bottom: -42px;
						left: 50%;
						width: 622px;
						height: 44px;
						font-family: YouSheBiaoTiHei;
						font-size: 14px;
						line-height: 16px;
						line-height: 44px;
						color: #ffffff;
						text-align: center;
						letter-spacing: 1px;
						background: url("./images/dataScreen-header-warn-bg.webp") no-repeat;
						background-size: 100% 100%;
						transform: translateX(-50%);
						z-index: 1;
						cursor: pointer;
					}
				}
			}
			.header-screening,
			.header-download {
				position: absolute;
				top: 0;
				right: 0;
				z-index: 9;
				box-sizing: border-box;
				width: 136px;
				height: 42px;
				padding-right: 4px;
				font-family: YouSheBiaoTiHei;
				font-size: 18px;
				font-weight: 400;
				line-height: 42px;
				color: #29fcff;
				text-align: center;
				cursor: pointer;
				background: url("./images/dataScreen-header-btn-bg-l.webp") no-repeat;
				background-size: 100% 100%;
			}
			.header-download {
				left: 0;
				padding-right: 0;
				background: url("./images/dataScreen-header-btn-bg-r.webp") no-repeat;
			}
			.header-playing,
			.header-time {
				position: absolute;
				top: 0;
				right: 108px;
				width: 310px;
				font-family: YouSheBiaoTiHei;
				font-size: 17px;
				font-weight: 400;
				line-height: 38px;
				color: #05e8fe;
				white-space: nowrap;
			}
			.header-playing {
				left: 108px;
			}
		}
		.dataScreen-main {
			box-sizing: border-box;
			display: flex;
			flex: 1;
			width: 100%;
			padding: 12px 42px 20px;
			.dataScreen-lf {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				width: 394px;
				height: 100%;
				margin-right: 40px;
				//left2right
				opacity: 0;
				transform: translateX(-100px); 
				animation-name: left2right;
				animation-duration: 1s;
				animation-iteration-count: 1;
				animation-delay: 2s;
				animation-fill-mode: forwards;
				@keyframes left2right {
					0% {
						opacity: 0;
						transform: translateX(-100px); 
					}
					100% {
						opacity: 1;
						transform: translateX(0);
					}
				}
				.dataScreen-top,
				.dataScreen-center,
				.dataScreen-bottom {
					position: relative;
					box-sizing: border-box;
					width: 100%;
					padding-top: 54px;
				}
				.dataScreen-top {
					height: 37%;
					background: url("./images/dataScreen-main-lt.webp") no-repeat;
					background-size: 100% 100%;
				}
				.dataScreen-center {
					height: 30%;
					background: url("./images/dataScreen-main-lc.webp") no-repeat;
					background-size: 100% 100%;
				}
				.dataScreen-bottom {
					height: 27%;
					margin-bottom: 0;
					background: url("./images/dataScreen-main-lb.webp") no-repeat;
					background-size: 100% 100%;
				}
			}
			.dataScreen-ct {
				display: flex;
				flex: 1;
				flex-direction: column;
				justify-content: space-between;
				height: 100%;
				margin-right: 40px;
				.dataScreen-map {
					position: relative;
					box-sizing: border-box;
					flex: 1;
					width: 100%;
					margin-top: 78px;
					.dataScreen-map-title {
						position: absolute;
						top: 10px;
						left: 10px;
						z-index: 99;
						box-sizing: border-box;
						display: flex;
						align-items: center;
						width: 270px;
						height: 25px;
						padding-left: 30px;
						font-size: 14px;
						color: #ffffff;
						letter-spacing: 5px;
						background: url("./images/map-title-bg.webp") no-repeat;
						background-size: 100% 100%;
						//zero2one
						opacity: 0;
						animation-name: zero2one;
						animation-duration: 1s;
						animation-iteration-count: 1;
						animation-delay: 2.5s;
						animation-fill-mode: forwards;
						@keyframes zero2one {
							0% {
								opacity: 0;
								transform: scale(0);
							}
							100% {
								opacity: 1;
								transform: scale(1);
							}
						}
					}
					.dataScreen-alarm {
						position: absolute;
						top: 0;
						left: 0;
						z-index: 99;
						box-sizing: border-box;
						width: 100%;
						height: 76px;
						padding: 2px 30px;
						overflow: hidden;
						background: url("./images/dataScreen-warn-bg.webp") no-repeat;
						background-size: 100% 100%;
						.map-item {
							display: flex;
							align-items: center;
							height: 37px;
							cursor: pointer;
							img {
								width: 15px;
								height: 15px;
								margin-top: 3px;
								margin-right: 6px;
							}
							span {
								font-size: 18px;
								color: rgb(255 183 0 / 74.7%);
							}
						}
					}
				}
				.dataScreen-cb {
					position: relative;
					box-sizing: border-box;
					width: 100%;
					height: 252px;
					padding-top: 54px;
					background: url("./images/dataScreen-main-cb.webp") no-repeat;
					background-size: 100% 100%;
					//bottom2top
					opacity: 0;
					transform: translateY(100px); 
					animation-name: bottom2top;
					animation-duration: 1s;
					animation-iteration-count: 1;
					animation-delay: 2s;
					animation-fill-mode: forwards;
					@keyframes bottom2top {
						0% {
							opacity: 0;
							transform: translateY(100px); 
						}
						100% {
							opacity: 1;
							transform: translateY(0);
						}
					}
				}
			}
			.dataScreen-rg {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				width: 394px;
				height: 100%;
				//right2left
				opacity: 0;
				transform: translateX(100px); 
				animation-name: right2left;
				animation-duration: 1s;
				animation-iteration-count: 1;
				animation-delay: 2s;
				animation-fill-mode: forwards;
				@keyframes right2left {
					0% {
						opacity: 0;
						transform: translateX(100px); 
					}
					100% {
						opacity: 1;
						transform: translateX(0);
					}
				}
				.dataScreen-top,
				.dataScreen-center,
				.dataScreen-bottom {
					position: relative;
					box-sizing: border-box;
					width: 100%;
					padding-top: 54px;
				}
				.dataScreen-top {
					height: 37%;
					background: url("./images/dataScreen-main-rt.webp") no-repeat;
					background-size: 100% 100%;
				}
				.dataScreen-center {
					height: 30%;
					background: url("./images/dataScreen-main-rc.webp") no-repeat;
					background-size: 100% 100%;
				}
				.dataScreen-bottom {
					height: 27%;
					margin-bottom: 0;
					background: url("./images/dataScreen-main-rb.webp") no-repeat;
					background-size: 100% 100%;
				}
			}
			.dataScreen-main-title {
				position: absolute;
				top: 1px;
				left: 0;
				display: flex;
				flex-direction: column;
				span {
					margin-bottom: 12px;
					font-family: YouSheBiaoTiHei;
					font-size: 18px;
					line-height: 16px;
					color: #ffffff;
					letter-spacing: 1px;
				}
				img {
					width: 68px;
					height: 7px;
				}
			}
			.dataScreen-main-chart {
				width: 100%;
				height: 100%;
			}
		}
	}
}
